export default {
	data() {
		return {
			scenicId: 0,
			loading_class: '',
			imgList: [],
			upImgList: [],
			videoPath: '',
			notesInfo: {
				notes_text: ''
			}
		}
	},
	onLoad: function(option) {
		this.scenicId = option.scenicId
	},
	methods: {
		async save() {
			var _this = this

			// 判断数据是否非空
			if (this.imgList.length == 0 || this.videoPath === '' || this.notesInfo.notes_text === '') {
				uni.showToast({
					title: '请完善内容哦！',
					duration: 2000,
					icon: 'none'
				})
				return
			}

			this.loading_class = 'cuIcon-loading2'

			// 上传图片
			var i=0
			await this.imgList.forEach((item, index) => {
				uni.uploadFile({
					url: _this.$BASE_HOST + 'file/upload', //仅为示例，非真实的接口地址
					filePath: item,
					name: 'file',
					success: (res) => {
						var r = JSON.parse(res.data)
						console.log(r)
						_this.upImgList.push(r.data.path)
					},
					fail(err) {
						uni.showToast({
							title: '上传失败！',
							duration: 2000,
							icon: 'none'
						})
					},
					complete() {
						i = i+1
						if (i===_this.imgList.length){
							// 上传视频 ===============================================================
							uni.uploadFile({
								url: _this.$BASE_HOST + 'file/upload',
								filePath: _this.videoPath,
								name: 'file',
								success: (res) => {
									var r = JSON.parse(res.data)
									_this.videoPath = r.data.path
								},
								complete() {
									// 将评论信息上传到服务器=========================================================
									uni.request({
										url: _this.$BASE_HOST + 'travelnotes/add',
										method: 'POST',
										data: {
											userId: uni.getStorageSync('local_userinfo').id,
											scenicId: _this.scenicId,
											noteText: _this.notesInfo.notes_text,
											noteImg: _this.upImgList.join(','),
											noteVideo: _this.videoPath
										},
										success: (res) => {
											uni.navigateBack({
												delta: 1
											});
										},
										fail(err) {
											uni.showToast({
												title: '发布失败！',
												duration: 2000,
												icon: 'none'
											})
										}
									});
								}
							});
						}
					}
				});
			})

			this.loading_class = ''
		},
		ChooseVideo() {
			var _this = this
			uni.chooseVideo({
				count: 1,
				sourceType: ['camera', 'album'],
				success: function (res) {
					console.log(res)
					_this.videoPath = res.tempFilePath
				},
				fail(err) {
					console.log(err)
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		ChooseImage() {
			uni.chooseImage({
				count: 9, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					console.log(res)
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
			console.log(this.imgList)
		},
		DelImg(e) {
			uni.showModal({
				title: '亲',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
	}
}
